body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  background-color: rgb(243, 180, 97);
  position: relative;
  width: 100vw;
  transition: 0.4s;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;
}
body::-webkit-scrollbar {
  display: none;
}


h1, h2, h4 {
  margin: 10px;
}
.newsBody {
  margin-top: 4px;
  padding: 5px;
  /* background-color: grey */
  width: 70vw;
  text-align:center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 10px;
  background-color: antiquewhite;
  border-radius: 10px;

  animation: fadeInRight 1s;
  transition-duration: 0.1s;
}
.newsBody:hover {
  transform: scale(1.01);
  box-shadow:  5px 5px 10px rgb(78, 66, 53),
             -5px -5px 10px rgb(72, 59, 41);
}
.overlay {
  position: fixed; 

  width: 50%;
  left: 0;
  height: 100%; 
  background: rgb(71, 53, 25);
  opacity: 0.5; 
  z-index: -10;
}
.newsBodyAnim {
  animation: fadeInRight 1s;
}
.text {
  display: flex;
  flex-direction: column;
}
.textAndImage {
  display: flex;
}
img {
  width: 40%;
}


.categoriesNavBar {
  display: flex;
  flex-direction: column;
  list-style: none;
  position: fixed;
  top: 0;
  left: 0;
  animation: fadeInLeft 1.5s;
  width: 40%;
  height: 100vh;
  background: black;
  transition: 0.3s;
  z-index:  1;
}
.categoryButtons {
  height: 6vh;
  width:100%;
  transition: 0.1s;
  border: none;
  background: none;
  color: white;
}
.categoryButtons:hover {
 margin-left: 20px;
 height: 30px;
 background-color: rgba(35, 42, 42, 0.19);
 backdrop-filter: blur(1.5rem);
}

.navBarHidden {
   width: 0;
   overflow: hidden;
}
.navButton {
  color: red;
  position: fixed;
  height: 6vh;
  left: 0vw;
  top: 0;
  background: black;
  border: none;
  border-top-right-radius: 5px;
  width: 20vw;
  border-bottom-right-radius: 5px;
  z-index: 10;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px)
  }
  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

li {
  list-style: none;
  margin: 20px;
}
.search {
  position: fixed;
  right: 0px;
  width: 20vw;
  top: 0px;
  height: 40px;
  font-size: 1rem;
  background: black;
  color: white;
  border-top-left-radius:  5px;
  border-bottom-left-radius: 5px;
  border: none;
}



@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(100px)
  }
  100% {
    opacity: 1;
    transform: translateX(0)
  }
}


.scrollFade {
  opacity: 1;
  pointer-events: all;
}
.scrollFade--hidden {
  opacity: 0;
  pointer-events: none;
}
.scrollFade--visible {
  opacity: 1;
  pointer-events: all;
}
.scrollFade--animate {
  transition: opacity 0.4s ease-in-out;
}

.selected {
  background-color: rgb(28, 193, 235);
  border: none;
}


@media only screen and (max-width: 700px) {
  .textAndImage {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0;
  }
  img {
      width: 50%;
  }
  .newsBody {
    line-height: 25px;
    width: 90vw;
    line-height: 20px;
  }
}
